// src/App.js
import React from "react";
import Header from "./components/Header";
import ProductList from "./components/ProductList";
import CartPage from "./components/CartPage";

function App() {
  return (
    <>
      <Header />
      <div style={{ maxWidth: 1100, margin: "24px auto", padding: 16 }}>
        <React.Suspense fallback={<div>Loading...</div>}>
          {/* Routes are rendered in index.js Router */}
          {/* Keep App simple; routing handled by Router wrapper in index.js */}
          {/* If you had nested routes, place them here via useRoutes or Routes as needed */}
          <ProductList />
        </React.Suspense>
      </div>
    </>
  );
}

export default App;
